.p {
    width: 30%;
    height: 30vh;
    background-color: white;
    margin: 20px 10px;
    border: 2px solid rgb(226, 226, 226);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser {
    height: 20px;
    background-image: linear-gradient(to right top, #afafaf, #b3c3d0, #a8dbe1, #b3f2d6, #eaffbc);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.p-img {
    width: 100%;
    transition: all 10s ease;
}

.p:hover .p-img{
    transform: translateY(-50%);
}

@media screen and (max-width: 480px) {
    .p {
        width: 200px;
        height: 10vh;
        flex-direction: column;
    }
}